                </el-date-picker>
              </el-col>
              <el-col :xs="6" :sm="6" :md="6" :lg="5" :xl="4">
                <el-select size="mini" style="width: 100%" v-model="listQuery.subject" clearable placeholder="">
                  <el-option
                    v-for="item in subject"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
              <el-col class="hidden-md-and-down" :xs="6" :sm="6" :md="6" :lg="5" :xl="4">
                <dict v-model="listQuery.enrolSite" size="mini" dictType="dict_enrolSite" placeholder=""  ></dict>
              </el-col>
              <el-col :xs="6" :sm="6" :md="6" :lg="5" :xl="4">
                <Coach v-model="listQuery.fieldCoach" size="mini" coachType="field" placeholder=""  ></Coach>
              </el-col>
              <el-col :xs="6" :sm="6" :md="6" :lg="5" :xl="4">
                <Coach v-model="listQuery.roadCoach" size="mini" coachType="road" placeholder=""  ></Coach>
              </el-col>
              <el-col class="hidden-md-and-down"  :xs="6" :sm="6" :md="6" :lg="5" :xl="3">
                <dict v-model="listQuery.source" size="mini" dictType="dict_source" placeholder=""  ></dict>
              </el-col>
              <el-col :xs="6" :sm="6" :md="6" :lg="3" :xl="3">

                <el-select style="width: 100%" size="mini" v-model="listQuery.motorcycleType" clearable placeholder="">
                  <el-option
                    v-for="item in $store.state.app.motorcycleType"
                    :key="item"
                    :label="item"
                    :value="item">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :xs="6" :sm="6" :md="6" :lg="5" :xl="4">
                <el-input @keyup.enter.native="searchClick" size="mini" placeholder="//" v-model="listQuery.condition"></el-input>
              </el-col>
              <el-col :xs="2" :sm="2" :md="2" :lg="2" :xl="2">
                <el-button type="primary"  size="mini" @click="searchClick"><i class="el-icon-search"></i></el-button>
              </el-col>
            </el-row>
          </el-card>

          <el-card :style="{height: ($store.state.app.client.height-135) + 'px'}">
            <el-table :data="stuList" :height="($store.state.app.client.height-260)" highlight-current-row stripe @row-dblclick="editList" v-loading="listLoading" element-loading-text="">
              <el-table-column align="center" label="" min-width="150px">
                <template slot-scope="scope">
                  <!--  -->
                  <el-row>
                    <el-tag class="img">
                      <img :src="scope.row.avatar" class="img">
                    </el-tag>
                  </el-row>
                  <el-row>
                    <el-col style="color: #7c7c7c;text-align: center;font-size: 12px;">{{scope.row.name}}</el-col>
                  </el-row>
                  <el-row>
                    <el-col style="color: #7c7c7c;text-align: center;font-size: 12px;">{{scope.row.mobile}}</el-col>
                  </el-row>
                </template>
              </el-table-column>
              <el-table-column align="center" label="" min-width="230px">
                <template slot-scope="scope" >
                  <!--  -->
                  <el-col style=" line-height: 25px">
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.idNumber}}</el-col>
                    </el-row>
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.sex | sexFilter}}</el-col>
                    </el-row>
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.state | subjectFilter}}</el-col>
                    </el-row>

                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">
                        <span v-for="(introducer,index) in scope.row.introducerList">{{introducer}}<span v-if="scope.row.introducerList.length !== (index+1)"></span></span>
                      </el-col>
                    </el-row>
                  </el-col>
                </template>
              </el-table-column>

              <el-table-column align="center" label="" min-width="230px">
                <template slot-scope="scope">
                  <el-col style=" line-height: 25px">
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.archivesNumber}}</el-col>
                    </el-row>
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.enrolTime | subTime}}</el-col>
                    </el-row>
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.periods}}</el-col>
                    </el-row>
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.motorcycleType}}</el-col>
                    </el-row>
                  </el-col>
                </template>
              </el-table-column>

              <el-table-column align="center" label="" min-width="230px">
                <template slot-scope="scope">

                  <el-col style=" line-height: 25px">
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.campus}}</el-col>
                    </el-row>
                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.source}}</el-col>
                    </el-row>

                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.fieldName}}</el-col>
                    </el-row>

                    <el-row :gutter="10">
                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">{{scope.row.roadName}}</el-col>
                    </el-row>
                  </el-col>
                </template>
              </el-table-column>

