                      <el-col :span="7" class="table_text">:</el-col>
                      <el-col :span="17" class="table_text">
                        <span v-for="(increment,index) in scope.row.incrementList">{{increment}}<span v-if="scope.row.incrementList.length !== (index+1)"></span></span>
                      </el-col>
                    </el-row>

