                  <template slot-scope="scope">
                    <span>{{ scope.row.examTime | subTime}}</span>
                  </template>
                </el-table-column>
                <el-table-column align="center"  label="">
                  <template slot-scope="scope">
                    <span>{{ scope.row.subject | subjectFilter}}</span>
                  </template>
                </el-table-column>
                <el-table-column align="center"  label="">
                  <template slot-scope="scope">
                    <span><{{scope.row.examTime | subTime}}> {{scope.row.examField}}</span>
                  </template>
                </el-table-column>
                <el-table-column align="center"  label="">
                  <template slot-scope="scope">
                    <el-tag v-show="scope.row.examState === '1'" type="info" style="color: #fff;" color="#67C23A"> </el-tag>
                    <el-tag v-show="scope.row.examState === '2'" type="info" style="color: #fff;" color="#F56C6C"> </el-tag>
                    <el-tag v-show="scope.row.examState === '3'" type="info" style="color: #fff;" color="#E6A23C"> </el-tag>
                  </template>
                </el-table-column>

              </el-table>

            </el-tab-pane>
            <!--<el-tab-pane label="" name="3">-->


            <!--</el-tab-pane>-->
            <el-tab-pane label="" name="4">
