                          <div style="padding-left: 16px;font-size: 12px;" >{{student.email}}</div>
                        </el-form-item>
                      </el-row>
                    </el-col>
                    <el-col :span="12">
                      <!--  -->
                      <el-row>
                        <el-form-item prop="position">
                          <span slot="label" class="text_css">:</span>
                          <div style="padding-left: 16px;font-size: 12px;" >{{student.position}}</div>
                        </el-form-item>
                      </el-row>
                    </el-col>
                  </el-row>
                </el-row>


                <el-row style="padding: 0 10px">
                  <el-row :gutter="10">
                    <el-col :span="12">
                      <!--  -->
                      <el-row >
                        <el-form-item prop="physicalExamination">
                          <span slot="label" class="text_css">:</span>
                          <div style="padding-left: 16px;font-size: 12px;" >{{student.physicalExamination === '1'?'':''}}</div>
                        </el-form-item>
                      </el-row>
                    </el-col>
                    <el-col :span="12">
                      <!--  -->
                      <el-row>
                        <el-form-item prop="addDrive">
                          <span slot="label" class="text_css">:</span>
                          <div style="padding-left: 16px;font-size: 12px;" >{{student.addDrive === '1'?'':''}}</div>
                        </el-form-item>
                      </el-row>
                    </el-col>
                  </el-row>
                </el-row>

                <el-row style="padding: 0 10px">
                  <el-row :gutter="10">
                    <el-col :span="12">
                      <!--  -->
                      <el-row>
                        <el-form-item prop="haveCar">
                          <span slot="label" class="text_css">:</span>
                          <div style="padding-left: 16px;font-size: 12px;" >{{student.haveCar === '1'?'':''}}</div>
                        </el-form-item>
                      </el-row>
                    </el-col>
                  </el-row>
                </el-row>

                <!--  -->
                <el-row style="margin: 0 10px">
                  <el-form-item prop="company">
                    <span slot="label" class="text_css">:</span>
                    <div style="padding-left: 16px;font-size: 12px;" >{{student.company}}</div>
                  </el-form-item>
                </el-row>


                <!--  -->
                <el-row style="margin: 0 10px">
                  <el-form-item prop="contactAddress">
                    <span slot="label" class="text_css">:</span>
                    <div style="padding-left: 16px;font-size: 12px;" >{{student.contactAddress}}</div>
                  </el-form-item>
                </el-row>

              </el-row>





            </el-card>
          </el-form>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
  import Coach from '@/components/Coach'
  import { removeAllSpace } from '@/utils/validate'
  import { mapGetters } from 'vuex'

  import { fetchStudentList, getStudent } from '@/api/student/student'
  import { examFetchList } from '@/api/student/examnote'

  import { getVehiclePeriodByStudentId } from '@/api/bespeak/vehicleperiod'
  import { getShuttleLogByStudentId } from '@/api/bespeak/shuttlestudent'
  import { followUpList } from '@/api/visit/followup'

  export default {
    name: 'table_student',
    components: {
      Coach
    },
    data() {
      return {
        student: {
          studentId: null,
          orgId: null,
          openId: null,
          archivesNumber: null,
          name: null,
          sex: null,
          age: null,
          idNumber: null,
          birthday: null,
          mobile: null,
          phone: null,
          email: null,
          wechat: null,
          avatar: null,
          contactAddress: null,
          campus: null,
          company: null,
          position: null,
          enrolTime: null,
          periods: null,
          studyTime: null,
          latitude: null,
          longitude: null,
          physicalExamination: null,
          haveCar: null,
          addDrive: null,
          state: null,
          graduationTime: null,
          periodOfValidity: null,
          aboardTime: null,
          roadCoach: null,
          fieldCoach: null,
          incrementList: [],
          introducerList: [],
          serviceType: null,
          arrearage: null,
          enrolSite: null,
          source: null,
          motorcycleType: null,
          delFlag: null,
          remark: null,
          operator: null,
          createTime: null,
          updateTime: null
        },
        stuList: [],
        examNoteList: [],
        vehiclePeriodList: [],
        shuttleLogList: [],
        followUpList: [],
        total: 1,
        listLoading: true,
        btnLoading: false,
        showModule: 'list',
        listQuery: {
          page: 1,
          limit: 20,
          condition: null,
          interval: [],
          subject: '5',
          roadCoach: null,
          fieldCoach: null,
          source: null,
          motorcycleType: null,
          orgId: null
        },
        activeName: '1',
        edit: false,
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date(new Date().setHours(0, 0, 0, 0))
              const start = new Date(new Date().setHours(0, 0, 0, 0))
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 3)
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date()
              const day = new Date(new Date().setHours(0, 0, 0, 0))
              var week = day.getDay()
              if (week === 0) week = 7
              const start = day
              start.setTime(start.getTime() - 3600 * 1000 * 24 * (week - 1))
              picker.$emit('pick', [start, end])
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().setHours(0, 0, 0, 0))
              start.setDate(1)
              picker.$emit('pick', [start, end])
            }
          }]
        },
        subject: [
          {
            value: '5',
            label: ''
          }, {
            value: '-1',
            label: ''
          }
        ],
        dialogFormBespeak: false,
        batchList: [],
        examBespeak: {
          studentId: null,
          state: 0,
          examId: null,
          subject: null
        },
        batchListQuery: {
          page: 1,
          limit: 0,
          /*  before    after */
          scope: 'after',
          subject: null
        },
        /*  */
        createLoading: true,
        infoLoading: true,
        userList: []
      }
    },
    created() {
      this.getList()
    },
    computed: {
      ...mapGetters([
        'permissions',
        'client'
      ]),
      sexVO() {
        const typeMap = {
          1: '',
          0: ''
        }
        return typeMap[this.student.sex]
      }
    },
    methods: {
      // id
      searchByOrg(data) {
        this.listQuery.page = 1
        this.getList()
      },
      //   
      editList(val) {
        this.infoLoading = true
        getStudent(val.studentId).then(response => {
          this.student = response.data.data
          this.examBespeak.studentId = this.student.studentId
          if (this.student.introducerIdList === null) this.student.introducerIdList = [] // null
          this.getExam()
          this.infoLoading = false
        })
        this.showModule = 'info'
      },
      /*  */
      handleClick(tab, event) {
        if (tab.label === '') {
          this.getExam()
        } else if (tab.label === '') {
          // this.getExam()
        } else if (tab.label === '') {
          this.getFollowUpList()
        } else if (tab.label === '') {
          this.getVehiclePeriod()
        } else if (tab.label === '') {
          this.getShuttleLog()
        }
      },
      /*  */
      getExam() {
        examFetchList({ studentId: this.student.studentId, examState: 'exam_note_true' }).then(response => {
          this.examNoteList = response.data.data.list
        })
      },
      /*  */
      getVehiclePeriod() {
        getVehiclePeriodByStudentId(this.student.studentId).then(response => {
          this.vehiclePeriodList = response.data.list
        })
      },
      /*  */
      getShuttleLog() {
        getShuttleLogByStudentId(this.student.studentId).then(response => {
          this.shuttleLogList = response.data.list
        })
      },
      /*  */
      getFollowUpList() {
        followUpList({ 'mobile': this.student.mobile }).then(response => {
          this.followUpList = response.data.data
        })
      },
      // 
      getList() {
        this.listLoading = true
        fetchStudentList(this.listQuery).then(response => {
          this.stuList = response.data.data.list
          this.total = response.data.data.totalCount
          this.listLoading = false
        })
      },
      handleSizeChange(val) {
        this.listQuery.limit = val
        this.getList()
      },
      handleCurrentChange(val) {
        this.listQuery.page = val
        this.getList()
      },
      // 
      backClick() {
        this.showModule = 'list'
        this.edit = false
        this.getList()
      },
      // 
      searchClick() {
        this.listQuery.page = 1
        this.examTimeBlur()
        this.listQuery.condition = removeAllSpace(this.listQuery.condition)
        this.getList()
      },
      examTimeBlur() {
        if (this.listQuery.interval === null) {
          this.listQuery.interval = []
          this.listQuery.beginTime = null
          this.listQuery.endTime = null
        }
        if (this.listQuery.interval.length !== 0) {
          this.listQuery.beginTime = this.listQuery.interval[0]
          this.listQuery.endTime = this.listQuery.interval[1]
        }
      }
    }
  }
</script>

<style>

  .img{
    width: 50px;
    height: 50px;
    padding: 0;
    border-radius: 150px;
    overflow: hidden;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .title{
    height: 40px;
    line-height: 40px;
    color:#495060;
    background-color: #f5f7fa;
    text-align: center;
    font-size: 16px;
    margin-bottom: 10px;
  }
  .text_css{
    color:#495060;
    font-size: 12px;
    word-break:keep-all;/*  */
    white-space:nowrap;/*  */
    overflow:hidden;/*  */
    text-overflow:ellipsis;/* (...) overflow:hidden;*/
  }
  .table_text{
    color: #7c7c7c;font-size: 12px;
    text-align: left;
    word-break:keep-all;/*  */
    white-space:nowrap;/*  */
    overflow:hidden;/*  */
    text-overflow:ellipsis;/* (...) overflow:hidden;*/
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 140px;
    height: 180px;
    line-height: 180px;
    text-align: center;
  }
  .avatar {
    width: 140px;
    height: 180px;
    display: block;
  }
  .avatar_img {
    line-height: 180px;
    position: relative;
    margin: auto;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    width: 140px;
    height: 180px;
    display: block;
  }
  .AddAvatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .AddAvatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .AddAvatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 130px;
    line-height: 130px;
    text-align: center;
  }
  .AddAvatar {
    width: 100px;
    height: 130px;
    display: block;
  }
  .batchCss{
    background-color: rgba(64,158,255,.1);
    display: inline-block;
    padding: 0 10px;
    height: 32px;
    line-height: 30px;
    font-size: 12px;
    color: #409eff;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid rgba(64,158,255,.2);
    white-space: nowrap;
    cursor: pointer;
  }
  .batchCss_selected{
    background-color: rgba(103,194,58,.1);
    border-color: rgba(103,194,58,.2);
    color: #67c23a;
  }
  .batchCss:hover{
    background-color: rgba(103,194,58,.1);
    border-color: rgba(103,194,58,.2);
    color: #67c23a;
  }
</style>



