import request from '@/utils/request'
// 
export function fetchList(query) {
  return request({
    url: '/visit/intention/getPage',
    method: 'get',
    params: query
  })
}
// 
export function addObj(obj) {
  return request({
    url: '/visit/intention/save',
    method: 'post',
    data: obj
  })
}

export function getIntentionByWechat(query) {
  return request({
    url: '/visit/intention/queryIntentionByWechat',
    method: 'get',
    params: query
  })
}

export function getIntentionByMobile(query) {
  return request({
    url: '/visit/intention/queryIntentionByMobile',
    method: 'get',
    params: query
  })
}
// ID
export function getObj(id) {
  return request({
    url: '/visit/intention/' + id,
    method: 'get'
  })
}
// 
export function delObj(id) {
  return request({
    url: '/visit/intention/' + id,
    method: 'delete'
  })
}
// 
export function putObj(obj) {
  return request({
    url: '/visit/intention/',
    method: 'put',
    data: obj
  })
}
// 
export function putIntention(obj) {
  return request({
    url: '/visit/intention/redistribution',
    method: 'put',
    data: obj
  })
}
// 
export function getOperator(id) {
  return request({
    url: '/visit/intention/operator',
    method: 'get'
  })
}

// 
export function queryIndex() {
  return request({
    url: '/visit/intention/index',
    method: 'get'
  })
}



