import axios from 'axios'
import { Message } from 'element-ui'
import store from '../store'
import { getToken, setToken } from '@/utils/auth'
//  

// axios
const service = axios.create({
  // baseURL: process.env.BASE_API, // apibase_url
  timeout: 600000 // 
})

// request
service.interceptors.request.use(config => {
  if (store.getters.token) {
    config.headers['Authorization'] = 'Bearer ' + getToken() // token--['X-Token']key 
  }
  // config.headers['Content-Type'] = 'application/json;charset=UTF-8'
  return config
}, error => {
  // Do something with request error
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code === 500) {
      Message.error(res.msg)
    }
    // 
    if (res.code === 500) {
      Message.error(res.message)
    }
    if (res.code === 107) {
      setToken(null)
      Message.error(res.message)
    }
    if (res.code === 0 && res.msg !== 'success') {
      Message.success(res.msg)
    }
    store.dispatch('setLoading', false)
    return response
  },
  error => {
    const res = error.response
    console.log(123456789)
    console.log(res)
    if (res) {
      if (res.status === 478 || res.status === 403) {
        Message.error('')
      } else if (res.status === 400) {
        if (res.data) {
          Message.error(res.data.error_description)
        } else {
          Message.error('')
        }
      } else if (res.status === 404) {
        Message.warning('!')
      } else if (res.status === 401) {
        Message.warning('!')
      } else if (res.status === 503) {
        Message.warning('!')
      } else {
        Message.error(res.msg)
      }
    }
    store.dispatch('setLoading', false)
  }
)

export function message(text, type, title) {
  // 
  Message({
    title: title,
    type: type, //  success/warning/info/error
    message: text, // 
    showClose: false, // 
    duration: 3 * 1000 // ,  0 
  })
}

export default service



