import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import { getToken } from '@/utils/auth'

const whiteList = ['/login'] // 

router.beforeEach((to, from, next) => { // Progress
  NProgress.start()
  if (getToken()) { // token
    if (to.path === '/login') {
      next({ path: '/' })
    } else {
      if (store.getters.roles && store.getters.roles.length > 0) { // user_info
        next()
      } else {
        getInfo(to, from, next)
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // 
      next()
    } else {
      next({ path: '/login' }) // 
    }
  }
})

export function getInfo(to, from, next) {
  store.dispatch('GetInfo').then(res => { // 
    const data = res.data
    store.dispatch('GenerateRoutes', data).then(() => { // 
      router.addRoutes(store.getters.addRouters) // 
      console.log(to)
      next({ ...to }) // hack addRoutes
    })
  }).catch((e) => {
    store.dispatch('FedLogOut').then(() => {
      next({ path: '/login' })
    })
  })
}

router.afterEach(() => {
  NProgress.done() // Progress
})



