import Layout from '../views/desktop/Layout'

/**
* hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
* redirect: noredirect           if `redirect:noredirect` will no redirct in the breadcrumb
* name:'router-name'             the name is used by <keep-alive> (must set!!!)
* meta : {
    title: 'title'               the name show in submenu and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar,
  }
**/
export const constantRouterMap = [
  { path: '/login', name: '', component: _import('login/index'), isShow: false, children: [] },
  { path: '/404', name: '404', component: _import('404'), isShow: false, children: [] },
  {
    menuId: 0,
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    isShow: false,
    meta: {
      title: '',
      icon: 'menu'
    },
    children: [
      { menuId: 0, path: 'dashboard', component: _import('dashboard/index'), isShow: false, name: '' }
    ]
  }
]
console.log(constantRouterMap)
export default new Router({
  // mode: 'history', // 
  // scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})
